import React, { useState, useEffect } from 'react'
import styled, { keyframes } from 'styled-components'
import { Text, Heading } from '@pancakeswap-libs/uikit'

const RainbowLight = keyframes`
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
`

const StyledCardAccent = styled.div`
  background: linear-gradient(45deg,
  rgba(255, 0, 0, 1) 0%,
  rgba(255, 154, 0, 1) 10%,
  rgba(208, 222, 33, 1) 20%,
  rgba(79, 220, 74, 1) 30%,
  rgba(63, 218, 216, 1) 40%,
  rgba(47, 201, 226, 1) 50%,
  rgba(28, 127, 238, 1) 60%,
  rgba(95, 21, 242, 1) 70%,
  rgba(186, 12, 248, 1) 80%,
  rgba(251, 7, 217, 1) 90%,
  rgba(255, 0, 0, 1) 100%);
  background-size: 300% 300%;
  animation: ${RainbowLight} 5s linear infinite;
  border-radius: 16px;
  filter: blur(6px);
  position: absolute;
  top: -2px;
  right: -2px;
  bottom: -2px;
  left: -2px;
  z-index: -1;
`

const FCard = styled.div`
  align-self: baseline;
  background: ${(props) => props.theme.card.background};
  border-radius: 32px;
  box-shadow: 0px 2px 12px -8px rgba(25, 19, 38, 0.1), 0px 1px 1px rgba(25, 19, 38, 0.05);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 24px;
  position: relative;
  text-align: center;
  margin-bottom: 50px;
`

const TimerCard = () => {
    const timeX = new Date("2021-06-30T09:00:00Z").getTime()
    
    const TimeLeft = () => {
        const now = Date.now()
        return timeX - now
    }

    const Format = (timeLeft: number) => {
        const d = new Date(Math.trunc(timeLeft / 1000) * 1000)
        const days = d.getUTCDate() > 1 ? `${d.getUTCDate() - 1} days, ` : ``
        return `${days}${d.getUTCHours()} hours, ${d.getUTCMinutes()} minutes, ${d.getUTCSeconds()} seconds`
    }
    
    const [ timeLeft, setTimeLeft ] = useState(TimeLeft())
    useEffect(() => {
        const myInterval = setInterval(() => {
            const t = TimeLeft()
            if (t > 0) {
                setTimeLeft(t)
            } else {
                clearInterval(myInterval)
            }    
        }, 1000)

        return () => {
            clearInterval(myInterval)
        }
    })

    return (
        <div>
        { timeLeft > 0 ? 
            <FCard>
                <StyledCardAccent />
                <Heading as="h1" size="lg" mb="14px" color="#507a24">
                    CLOVER Minting will start in
                </Heading>
                <Heading size="md" mb="14px" color="#507a24">
                    {Format(timeLeft)}
                </Heading>
                <Text style={{ textAlign: 'start' }}>After the start:</Text>
                <Text style={{ textAlign: 'start' }}>- you can buy CLOVER tokens for an initial price of $ 0.1</Text>
                <Text style={{ textAlign: 'start' }}>- you will farm CLOVER tokens for your lp and single tokens deposits</Text>
                <Text style={{ textAlign: 'start' }}><br/></Text>
                <Text style={{ textAlign: 'start' }}>Before that you can:</Text>
                <Text style={{ textAlign: 'start' }}>- use our referral program and get 1% from your friends&apos; deposits</Text>
                <Text style={{ textAlign: 'start' }}>- deposit your own tokens to get the largest percentage at the start</Text>
                <Text bold style={{ textAlign: 'start' }}><br/>* Attention! CLOVER token address is 0xb926F6C23749D2Da0426d0f6B36c96ed7fd3C69f. You cannot buy CLOVER tokens before minting starts. Be careful.</Text>
            </FCard>
            : null
        }
        </div>
    )
}

export default TimerCard