import React from 'react'
import styled from 'styled-components'
import BigNumber from 'bignumber.js/bignumber'
import { getCakeAddress } from 'utils/addressHelpers'
import { useTotalSupply, useBurnedBalance } from 'hooks/useTokenBalance'
import { Card, CardBody, Heading, Text } from '@pancakeswap-libs/uikit'
import useI18n from 'hooks/useI18n'
import { getBalanceNumber } from 'utils/formatBalance'
import { CAKE_PER_BLOCK } from 'config'
import { useTotalValue, usePriceCakeBusd } from '../../../state/hooks'
import CardValue from './CardValue'

const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`

const TotalValueLockedCard = () => {
  const TranslateString = useI18n()
  // const data = useGetStats()
  const totalValue = useTotalValue();
  // const tvl = totalValue.toFixed(2);
  const totalSupply = useTotalSupply()
  const burnedBalance = useBurnedBalance(getCakeAddress())
  const cloverPrice = usePriceCakeBusd();
  const circSupply = totalSupply ? totalSupply.minus(burnedBalance) : new BigNumber(0);
  const cakeSupply = getBalanceNumber(circSupply);
  const marketCap = cloverPrice.times(circSupply);
  const cloverPerBlock = CAKE_PER_BLOCK.div(new BigNumber(1e18))
  return (
    <StyledCakeStats>
      <CardBody>
        <Heading size="lg" mb="14px" mt="10px">
          {TranslateString(999, 'Total Value Locked (TVL)')}
        </Heading>
        <CardValue value={totalValue.toNumber()} prefix="$" decimals={2} />      

        <Heading size="lg" mb="14px" mt="30px">
          {TranslateString(534, 'Clover Stats')}
        </Heading>
        <Row>
          <Text fontSize="14px">{TranslateString(999, 'Market Cap')}</Text>
          <CardValue fontSize="14px" value={getBalanceNumber(marketCap)} decimals={0} prefix="$" />
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(536, 'Total CLOVER Supply')}</Text>
          {cakeSupply && <CardValue fontSize="14px" value={cakeSupply} decimals={0} />}
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(538, 'Total CLOVER Burned')}</Text>
          <CardValue fontSize="14px" value={getBalanceNumber(burnedBalance)} decimals={0} />
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(540, 'New CLOVER/block')}</Text>
          <Text bold fontSize="14px">{cloverPerBlock.toString()}</Text>
        </Row>

      </CardBody>
    </StyledCakeStats>
  )
}

export default TotalValueLockedCard
